import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CallCenterRoutingModule } from './call-center-routing.module';
import { SharedModule } from '@shared';
import { QueueComponent } from './queue/queue.component';
import { AgentsComponent } from './agents/agents.component';
import { TiersComponent } from './tiers/tiers.component';

const COMPONENTS = [QueueComponent,AgentsComponent ,TiersComponent];
const COMPONENTS_DYNAMIC = [];
@NgModule({
  declarations: [...COMPONENTS, ...COMPONENTS_DYNAMIC,QueueComponent , AgentsComponent, TiersComponent],
  imports: [
    SharedModule,
    CallCenterRoutingModule
  ],
  entryComponents: COMPONENTS_DYNAMIC,
})
export class CallCenterModule { }
